import React from 'react';
import { bajaj_awards, CNBC, ET_BFSI, great_place } from '../../../../assets/images';

export default () => (<section className="bajajawards pb60">
    <div className="container ">
        <div className=" row justify-content-between pb40">
            <div className="col-12 col-md-6 align-self-center ">
                <div className=" sec-heading">
                    <h2> Awards &amp; Recognition</h2>
                    Bajaj Capital is proud to have been recognised as a national leader in financial services, but don’t just take our word for it.
        </div>
            </div>
            <div className="col-12 col-md-6">
                <img src={bajaj_awards} className="img-fluid" alt="awards" /> </div>
        </div>
        <div className="row">
            <div className="col-12 col-md-4 ">
                <div className="box">
                    <h4><img src={CNBC} className="img-fluid" alt="economic times" /> </h4>
                    <p>
                        Best Performing National Financial Advisor Retail
          </p>
                    <span className="quote">
                        <i className="fas fa-quote-left" />
                    </span>'Best Performing National Financial Advisor - Retail'' at the CNBC TV 18 Financial Advisor Awards 2017 –
              2018
        </div>
            </div>
            <div className="col-12 col-md-4 ">
                <div className="box">
                    <h4><img src={ET_BFSI} className="img-fluid" alt="economic times" /> </h4>
                    <p>
                       Best BFSI Brand 2019 – Broking Category
          </p>
                    <span className="quote">
                        <i className="fas fa-quote-left" />
                    </span>The Economic Times recognises Bajaj Capital
              for being a symbol of excellence in NBFC category in 2016
        </div>
            </div>
            <div className="col-12 col-md-4 ">
                <div className="box">
                    <h4><img src={great_place} className="img-fluid" alt="economic times" /> </h4>
                    <p>
                        Great Place to Work Certification
          </p>
                    <span className="quote">
                        <i className="fas fa-quote-left" />
                    </span>Adjudged ''Best Place to Work'' in 2008, 2009, 2011, 2012 and 2019 by Great Place to Work Institute -
              India
        </div>
            </div>
        </div>
    </div>
</section>

)